import React from 'react'

const Stock = ({ items }) => {
    let stock_value = items.reduce((total, item) => total += item.price, 0)
    return (
        <div class="container">

            <h3>Current Stock</h3>
            <p>Total items in shop: {items.length}</p>
            <p>Total value of shop: {"£"+((stock_value/100).toFixed(2))}</p>

            <table class="table table-hover">
                <thead>
                    <tr>
                        <th scope="col">ID</th>
                        <th scope="col">Name</th>
                        <th style={{textAlign: 'center'}} scope="col">Price</th>
                        <th style={{textAlign: 'center'}} scope="col">Amount</th>
                    </tr>
                </thead>
                <tbody>
                   {items.map((item) => (
                   <tr>
                       <td>{item.item_id}</td>
                       <td>{item.item_name}</td>
                       <td style={{textAlign: 'center'}}>{"£"+((item.price/100).toFixed(2))}</td>
                       <td style={{textAlign: 'center'}}>{item.amount}</td>
                   </tr>
               ))} 
               </tbody>
            </table>
        </div>
    )
}

export default Stock;