import React, { Component } from 'react'
import { Redirect } from "react-router-dom";
import { useHistory } from "react-router-dom";

class Users extends Component {

    state = {
        "users": []
    }

    componentDidMount() {
        console.log("Fetching users...")
        fetch('https://santa-shares.azurewebsites.net/api/users')
            .then(res => res.json())
            .then(data => {
                data.sort(function (a, b) { return b.total - a.total; })
                this.setState({ "users": data })
            })
            .catch(console.log)
    }

    handleClick(user_id) {
        this.setState({ "redirect" : user_id })
    }

    render() {
        if (this.state.redirect !== undefined) return <Redirect to={'/users/' + this.state.redirect} />
        return (
            <div class="container">
                <h3>Leaderboard</h3>
                <table class="table table-hover">
                    <thead>
                        <tr>
                            <th scope="col">ID</th>
                            <th scope="col">Name</th>
                            <th style={{ textAlign: 'center' }} scope="col">Cash</th>
                            <th style={{ textAlign: 'center' }} scope="col">Stock</th>
                            <th style={{ textAlign: 'center' }} scope="col">Total</th>
                        </tr>
                    </thead>
                    <tbody>
                        {this.state.users.map((user) => (
                            <tr key={user.user_id} onClick={() => this.handleClick(user.user_id)}>
                                <td>{user.user_id}</td>
                                <td>{user.user_name}</td>
                                <td style={{ textAlign: 'center' }}>{"£" + ((user.balance / 100).toFixed(2))}</td>
                                <td style={{ textAlign: 'center' }}>{"£" + ((user.stock_value / 100).toFixed(2))}</td>
                                <td style={{ textAlign: 'center' }}>{"£" + ((user.total / 100).toFixed(2))}</td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        )
    }
}

export default Users;